const Page = () => {
  return (
    <TimerProvider>
      <GameWrapper />
    </TimerProvider>
  );
};

const GameWrapper = () => {
  const {
    currentLevel,
    items,
    aciertos,
    errores,
    completedSets,
    totalAciertos,
    currentGameLevel,
    isLastLevel,
    isLevelComplete,
    isGameComplete,
    handleDragStart,
    handleDrop,
    handleNextLevel,
    handleRestart,
    handleTiempoFinalizado
  } = useGameLogic();

  return (
    <GamesTemplate>
      <div className="max-w-6xl mx-auto pt-4">
        <GameHeader
          aciertos={aciertos}
          errores={errores}
          completedSets={completedSets.length}
          totalSets={currentGameLevel.sets.length}
          level={currentLevel + 1}
          totalAciertos={totalAciertos + aciertos}
        />

        <TiempoJuego position="top-right" formato="minutos" />
        <InformacionNivel currentLevel={currentLevel} gameLevel={currentGameLevel} />

        {isGameComplete ? (
          <JuegoCompletado aciertos={aciertos} onRestart={handleRestart} />
        ) : isLevelComplete ? (
          <NivelCompletado aciertos={aciertos} isLastLevel={isLastLevel} onNextLevel={handleNextLevel} />
        ) : (
          <AreaJuego
            items={items}
            currentGameLevel={currentGameLevel}
            completedSets={completedSets}
            onDragStart={handleDragStart}
            onDrop={handleDrop}
          />
        )}
      </div>
    </GamesTemplate>
  );
};
